import React from 'react';
import Profile from "./Profile";
import { connect } from "react-redux";
import { getStatus, getUserProfile, updateStatus, setProfilePhoto, setProfileData } from "../../redux/profile-reducer";
import { withRouter } from "react-router-dom";
import { compose } from "redux";

class ProfileContainer extends React.Component {

    refreshProfile() {
        let userId = this.props.match.params.userId;
        if (!userId) {
            userId = this.props.authorizedUserId;
            if (!userId) {
                this.props.history.push("/login");
            }
        }
        this.props.getUserProfile(userId);
        this.props.getStatus(userId);
    }

    componentDidMount() {
        this.refreshProfile()
    }

    componentDidUpdate(prevProps) {
        if (this.props.location !== prevProps.location ||
            prevProps.isAuth !== this.props.isAuth
        ) {
            this.refreshProfile()
        }
    }

    render() {
        return (
            <Profile {...this.props}
                profile={this.props.profile}
                status={this.props.status}
                updateStatus={this.props.updateStatus}
                setProfilePhoto={this.props.setProfilePhoto}
                location={this.props.location}
                setProfileData={this.props.setProfileData}
                nickname={this.props.nickname}
            />
        )
    }
}

let mapStateToProps = (state) => {
    return ({
        profile: state.profilePage.profile,
        status: state.profilePage.status,
        authorizedUserId: state.auth.userId,
        isAuth: state.auth.isAuth,
        nickname: state.auth.nickname
    })
}

export default compose(
    connect(mapStateToProps, { getUserProfile, getStatus, updateStatus, setProfilePhoto, setProfileData }),
    withRouter
)(ProfileContainer);




