import s from './Post.module.css';
import { onError } from '../../../../utils/onError';
import HOST from '../../../../env'

const Post = (props) => {
  return (
    <div className={s.item}>
      <img src={`${HOST}/${props.photo}`} onError={onError} />
      {props.message}
      <div>
        <span>like</span> {props.likesCount}
      </div>
    </div>
  )
}

export default Post