import React, { useState } from 'react';
import s from './ProfileInfo.module.css';
import s2 from '../../common/FormsControls/FormsControls.module.css';
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import { createField } from './../../common/FormsControls/FormsControls';
import { Input } from '../../common/FormsControls/FormsControls'
import { reduxForm } from 'redux-form';
import { required, boolRequired } from './../../../utils/validators/validators';
import { onError } from '../../../utils/onError';
import HOST from '../../../env'

const ProfileInfo = (props) => {
    let { status, updateStatus, profile, setProfilePhoto, location: { pathname }, setProfileData } = props
    let [editMode, setEditMode] = useState(false);


    if (!profile) {
        return <Preloader />
    }


    let setPhoto = (e) => {
        e.target.files.length && setProfilePhoto(e.target.files[0])
    }

    const onSubmit = (formData) => {
        setProfileData(formData).then(() => { setEditMode(false) })
    }

    return (
        <div>
            <div className={s.descriptionBlock}>
                <img src={`${HOST}/${profile.photo}`} className={s.userPhoto} onError={onError}/>
                <ProfileStatusWithHooks status={status} updateStatus={updateStatus} pathname={pathname}/>
                {pathname == "/profile" && <input type="file" onChange={setPhoto} />}

                {pathname == "/profile" && !editMode && <div><button onClick={() => { setEditMode(true) }}>Edit</button></div>}
                {editMode ? <EditProfileDataRF setEditMode={setEditMode} onSubmit={onSubmit} contacts={profile.contacts} /> : <ProfileData profile={profile} nickname={props.nickname} />}

            </div>
        </div>
    )
}

const EditProfileData = (props) => {
    let { handleSubmit, contacts, setEditMode, error } = props
    return <div>
        <button onClick={() => {setEditMode(false)}}>Close</button>
        <form onSubmit={handleSubmit}>
            {error && <div className={s2.formSummaryError}> {error} </div>}
            <button >Save</button>
            {createField('nickname', 'nickname', [required], Input)}
            {createField('fullName', 'fullName', [required], Input)}
            {createField('aboutMe', 'aboutMe', [required], Input)}
            {createField('lookingForAJob', 'lookingForAJob', [required, boolRequired], Input)}
            {createField('lookingForAJobDescription', 'lookingForAJobDescription', [required], Input)}
            {Object.keys(contacts).map(key => createField(key, 'contacts.' + key, [], Input))}
        </form>
    </div>
}

export const EditProfileDataRF = reduxForm({ form: 'profileData' })(EditProfileData)


export const ProfileData = (props) => {
    let { fullName, aboutMe, lookingForAJob, lookingForAJobDescription, contacts } = props.profile;
    return <>
    <div><b>nickname:</b> {props.nickname}</div>
        <div><b>fullName:</b> {fullName}</div>
        <div><b>aboutMe:</b> {aboutMe}</div>
        <div><b>lookingForAJob:</b> {lookingForAJob}</div>
        <div><b>lookingForAJobDescription:</b> {lookingForAJobDescription}</div>
        <br />
        <div><b>contacts:</b> {Object.keys(contacts).map(key => <div><b>{key}:</b> {contacts[key]}</div>)}</div>
    </>
}

export default ProfileInfo;